<script>
import axios from 'axios'

export default {
    name: 'AboutUsList',
    data () {
        return {
            aboutUsListInfoData: null,
        }
    },
    created() {
        axios
            .get('data/aboutUsInfo.json')
            .then(resp=>{
                this.aboutUsListInfoData = resp.data
            })
            .catch(err=>{
                this.$toast.error(err)
            })

    }
}
</script>